<template>
    <div id="app">
        <div class="column is-half is-offset-one-quarter">
            <img src="./assets/pokemon.png" />
            <hr>
            <h4 class="is-size-4">Pokedex</h4>
            <input class="input is-rounded" type="text" placeholder="Buscar pokemon pelo nome" v-model="search">
            <button class="button is-fullwidth is-success" id="buscaBtn" @click="buscar">Buscar</button>
            <div v-for="(poke, index) in filteredPokemons" :key="poke.url" >
                <Pokemon :name="poke.name" :url="poke.url" :num="index+1" />
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';

import Pokemon from './components/Pokemon';

export default {
    name: "App",
    data: function() {
        return {
            pokemons: [],
            filteredPokemons: [],
            search: ''
        }
    },
    created: function() {
        axios.get("https://pokeapi.co/api/v2/pokemon?limit=151&offset=0").then(resp => {
            this.pokemons = resp.data.results;
            this.filteredPokemons = resp.data.results;
        });
    },
    components: {
        Pokemon
    },
    methods: {
        buscar: function() {
            this.filteredPokemons = this.pokemons;
            if (this.search == '' || this.search == ' ') {
                this.filteredPokemons = this.pokemons;
            }
            else {
                this.filteredPokemons = this.pokemons.filter(pokemon => pokemon.name == this.search);
            }
        }
    },
};
</script>

<style>
#app {
    font-family: Avenir, Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
}

#buscaBtn {
    margin-top: 2%;
}
</style>
